@font-face {
  font-family: "Montserrat Black";
  src: url("./assets/fonts/montserrat-extrabold-webfont.woff2") format("woff2"),
    url("./assets/fonts/montserrat-extrabold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Merriweather italic";
  src: url("./assets/fonts/merriweather-italic-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Merriweather";
  src: url("./assets/fonts/merriweather-regular-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Merriweather";
  src: url("./assets/fonts/merriweather-black-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-black-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

html,
body {
  background-color: #282828 !important;
}

body {
  margin: 0;
  font-family: "Merriweather", serif !important;
  box-shadow: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat Black", sans-serif !important;
  font-weight: 900 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cover-container {
  margin-left: auto !important;
  max-width: 832px !important;
}

.logo {
  width: 2em !important;
  height: 2em !important;
  margin-left: -4px;
}

.text-lime {
  color: #a2fca2 !important;
}

footer a,
footer a:focus,
footer a:hover {
  color: #a2fca2 !important;
}
.blog-post ul {
  list-style: none;
  padding: 0;
}

.blog-post li:before {
  content: "â€¢";
  margin: 0 0.5rem 0 0;
  color: #a2fca2;
  font-size: 2rem;
  vertical-align: middle;
}

/*  Blog.css  */
.blog-post {
  font-size: 1.1rem !important;
  line-height: 2rem !important;
  margin-top: 2rem;
  margin-bottom: 4rem !important;
}
.blog-post-title {
  font-size: 2rem !important;
}
.blog-post-meta {
  font-size: 1rem !important;
}
.blog-post h4 {
  margin-top: 2.5rem !important;
  margin-bottom: 1rem !important;
}
/*  ---------  */

/*  Bootstrap  */
.navbar {
  padding: 0 !important;
}
/*  ---------  */

/* HighlighJS   */
.hljs {
  color: #ffffe6 !important;
  border-radius: 10px !important;
  background-color: #333 !important;
  padding: 1rem !important;
  margin-top: 1.75rem !important;
  margin-bottom: 2rem !important;
  line-height: 1.5 !important;
}
pre {
  box-shadow: 0 8px 16px #000 !important;
}

/*  ---------  */

@media (max-width: 576px) {
  .blog-post {
    margin-top: 1.5rem;
    line-height: 1.75rem !important;
  }
}
